}

.loader {
  width: 12px;
  height: 12px;
  color: #000;
  box-sizing: border-box;
  z-index: 1;
  border-radius: 50%;
  animation: animloader 2s linear infinite;
  display: none;
  position: absolute;
  top: 50%;
  left: 50%;
}

